@import url(https://fonts.googleapis.com/css2?family=Gugi&family=Noto+Sans+KR&family=Rampart+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --main-color:rgb(0, 0, 73);
  --sub-color:rgb(67, 255, 177);
  --font-family:'Zen Kurenaido','Gowun Dodum', sans-serif;
  --font-color:#edecff;
  --habit-button-color:#b5bac0;
}
body{
  height:100vh;
  margin:0;
  background: rgb(56,54,210);
  background: linear-gradient(302deg, rgba(56,54,210,1) 18%, rgba(212,58,90,0.8911939775910365) 100%);
  background-repeat: no-repeat;
  background-size: cover;
}
a{
  text-decoration: none;
  color:#edecff;
  color:var(--font-color);;
}
button{
  outline: none;
  border : 0;
  cursor: pointer;
}
*{
  box-sizing: border-box;
}
textarea  
{  
  font-family: 'Zen Kurenaido','Gowun Dodum', sans-serif;  
  font-family: var(--font-family);
  font-size: 12px;   
}

#filled-basic{
  color:black;
  background-color: white;
  font-size:20px;
}

.habit{
  display:flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 20px;
  font-size: 1.5rem;
  padding:0.5em;
  margin-bottom: 0.5em;
  margin-top: 0.5em;
  width: 80%;
  background: rgb(31,0,196);
  background: linear-gradient(144deg, rgba(31,0,196,1) 1%, rgba(45,0,145,1) 70%);
  box-shadow: 2px 2px 2px rgb(0, 0, 0); 
  transition:all 0.9s, color 0.3;
  cursor: pointer;
}
.habit:hover{
  box-shadow: 0 80px 0 0 rgba(0, 0, 0, 0.25) inset, 0 -80px 0 0 rgb(255, 0, 242) inset;
  color:rgb(67, 255, 177);
  color:var(--sub-color);
}
.habit-list ul{
  width:80%;
  display:flex;
  flex-direction: column;
  align-items: center;
}
.habit-buttons{
  display:flex;
  align-items: center;  
}
.habit-name{
  margin-bottom: 7px;
  width:400px;
}
.habit-count{
  width:100px;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.2em;
  margin-bottom: 7px;
  color: #edecff;
  color: var(--font-color);
}
.habit-button{
  font-size: 1.5rem;
  margin: 0 0.1em;
  background-color: transparent;
  color: #b5bac0;
  color: var(--habit-button-color);
}

.habit-button:hover{
  opacity: 0.8;
}

.habit-delete{
  color:#b5bac0;
  color:var(--habit-button-color);
  vertical-align: baseline;
  font-size: 1.5rem;
}

.clear-button,
.reset-button{
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
  color:rgb(67, 255, 177);
  color:var(--sub-color);
  left:150px;
  font-family: 'Zen Kurenaido','Gowun Dodum', sans-serif;
  font-family: var(--font-family);
}

.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(67, 255, 177);
  font-size: 2rem;
  padding: 8px 12px;
  font-family: 'Zen Kurenaido','Gowun Dodum', sans-serif;
  font-family: var(--font-family);
}
.navbar-logo{
  color: green;
}
.navbar-logo span{
  padding-left: 0.5em;
  color: black;
}
.navbar-logo span:hover{
  color:palevioletred;
}
.navbar-logo:hover{
  color:palevioletred;
}

.navbar-count{
  display:inline-block;
  text-align:center;
  font-size: 2rem;
  width:2.5rem;
  height: 2.5rem;
  line-height:2.5rem;
  margin-left:0.2em;
  background-color: green;
  color:#edecff;
  color:var(--font-color);
  border-radius:50%;
  padding-left: 0;
  margin-right:110px;
}
.navbar-count span{
  margin-left: 0;
}
.navbar-links{
  display:flex;
  list-style:none;
  color: black;
  padding:0;
}
.navbar-links li{
  padding-left:0.5em;
  
}
.navbar-links li:nth-child(2){
  
  padding-right:1em;
}
.navbar-links li:hover{
  color:palevioletred;
}


.submit-btn{
  margin-left:50px;
  
}

.input-form{
  display:flex;
  justify-content: center;
  margin-top:20px;
  align-items: center;
  
}
.input-form input{
  font-family: 'Zen Kurenaido','Gowun Dodum', sans-serif;
  font-family: var(--font-family);
}
.habit-list{
  display:flex;
  flex-direction: column;
  justify-content: center;
  width:100%;
  align-items: center;
  font-family: 'Zen Kurenaido','Gowun Dodum', sans-serif;
  font-family: var(--font-family);
  color:#edecff;
  color:var(--font-color);
}


@media screen and (max-width:768px){
  .navbar{
    flex-direction: column;
  }
  .navbar-count{
    margin-top:10px;
    
    margin-right:0;
  }
  .navbar-links{
    margin-top:2px;
    margin-bottom:1px;
  }

  .habit{
    width:80%;
  }
  .habit-list ul{
    width:100%;
  }
}
